body {
  color: white;
  font-family: Helvetica, Lucida, sans-serif;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;  
  background-color: #787834;
}
h1 {
  font-size: 2em;
  color: #000000;
}
.flex-container {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  width: 260px;
  border: solid 10px;
  border-radius: 10%;
  border-color: #0c0c0c;
  background-color: #0c0c0c;
}
.flex-container > .row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.flex-container > .row > div {
  font-size: 26px;
  text-align: center;
  border: solid 2px;
  border-color: black;
  padding: 15px;
  width:30px;
  height: 30px;
  border-radius: 20%;  
}
.flex-container > .row > .digits {
    background-image: radial-gradient(#000000, #454545);
}
.flex-container > .row > #display {
  width: 220px;
  background-image: radial-gradient(#020202, #202020);  
  font-weight: 900;
  text-align: right;
}
#clear {
  background-image: radial-gradient(#5a0000,#9d0000);
  text-align: center; 
  font-size: 1.4em;
}
#equals {
  font-weight: 900;
  background-image: radial-gradient(#006400, #228B22);
}
#add, #subtract, #multiply, #divide {
 background-image: radial-gradient(#0000a0, #0000e6);
}
#footer {
  margin-top: 1em;
}